<template>
  <div class="album">
    <Rotation></Rotation>
    <div id="myVideo">
      <video
        ref="myVideo"
        style="width: 100%; height: 100%"
        class="video-js vjs-default-skin vjs-big-play-centered"
      >
        <source src="../assets/resource/3.mp4" type="video/mp4" />
      </video>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Rotation from "@/components/rotation3d.vue";

export default {
  name: "Album",
  components: {
    Rotation,
  },
  mounted() {
    this.initVideo();
  },
  methods: {
    initVideo() {
      //初始化视频方法
      this.$video(this.$refs.myVideo, {
        //确定播放器是否具有用户可以与之交互的控件。没有控件，启动视频播放的唯一方法是使用autoplay属性或通过Player API。
        controls: true,
        //自动播放属性,muted:静音播放
        // autoplay: "muted",
        //建议浏览器是否应在<video>加载元素后立即开始下载视频数据。
        preload: "auto",
        poster: require("../assets/image/7.jpg"),
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.album {
  height: 100%;
  width: 100%;
  background: url("../assets/image/8.jpeg") no-repeat;
  background-size: 100% 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#myVideo {
  width: 100%;
  height: 211px;
  padding-top: 100px;
}
</style>
